import React, { useState, useEffect } from 'react';
import { supabase } from '../../supabase'; // 
import './Register.css';

function Register() {
  const [name, setUsername] = useState(''); // 
  const [pin, setPassword] = useState('');
  const [email, setEmail] = useState('');
  const [scope, setPermission] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const [owner, setOwner] = useState(false); //  owner 
  const [loginData, setLoginData] = useState([]);
  const [showRegisterPopup, setShowRegisterPopup] = useState(false);
  const [showConfirmationPopup, setShowConfirmationPopup] = useState(false);
  const [deletingUser, setDeletingUser] = useState('');
  const [editingUser, setEditingUser] = useState('');
  const [showEditPopup, setShowEditPopup] = useState(false);
  const [editName, setEditName] = useState('');
  const [editEmail, setEditEmail] = useState('');
  const [editScope, setEditPermission] = useState('');

  //  checkPermission 
  const checkPermission = async (name) => { // 
    try {
      // 
      const { data: allLoginData, error } = await supabase
        .from('login')
        .select('*');

      if (error) {
        throw error;
      }

      // 
      const login = allLoginData.find(user => user.name === name); // 
      if (login && login.scope) {
        setPermission(login.scope);

        if (login.scope !== 'owner') {
          //  owner
          console.log('Permission denied. Redirecting...');
          setOwner(false);
        } else {
          setOwner(true);
          console.log('You are owner!');
        }
      } else {
        console.log('User not found or scope not defined.');
        setOwner(false);
      }

      // 
      setLoginData(allLoginData);
    } catch (error) {
      console.error('Error checking permission:', error.message);
    }
  };

  useEffect(() => {
    let mounted = true; // 

    const usernameFromLocal = localStorage.getItem('username');
    if (usernameFromLocal) {
      checkPermission(usernameFromLocal); // 
    }

    // 
    return () => {
      mounted = false;
    };
  }, [name]); //  name 

  const handleRegister = async (event) => {
    event.preventDefault();

    try {
      // 
      if (!name || !pin) {
        throw new Error('Username and password are required.');
      }

      // Supabaselogin
      const { data, error } = await supabase
        .from('login')
        .insert([
          { name, pin, email}
        ])
        .select();

      if (error) {
        throw error;
      }

      console.log('Account registered successfully:', data);

      // 
      setUsername('');
      setPassword('');
      setEmail('');
      setPermission('');
      setErrorMessage('');
      // 
      setShowRegisterPopup(false);

      window.location.reload();
    } catch (error) {
      // 
      console.error('Error registering account:', error.message);
      setErrorMessage(error.message);
    }
  };

  const handleDelete = async () => {
    try {
      // Supabase
      const { data, error } = await supabase
        .from('login')
        .delete()
        .eq('name', deletingUser);

      if (error) {
        throw error;
      }

      console.log('Account deleted successfully:', data);

      // 
      setShowConfirmationPopup(false);

      // 
      const { data: refreshedData, error: refreshError } = await supabase
        .from('login')
        .select('*');

      if (refreshError) {
        throw refreshError;
      }

      setLoginData(refreshedData);
    } catch (error) {
      console.error('Error deleting account:', error.message);
      setErrorMessage(error.message);
    }
  };

  const handleEdit = async (event) => {
    event.preventDefault();

    try {
      // Supabase
      const { data, error } = await supabase
        .from('login')
        .update({ name: editName, email: editEmail})
        .eq('name', editingUser);

      if (error) {
        throw error;
      }

      console.log('Account updated successfully:', data);

      // 
      setShowEditPopup(false);

      // 
      const { data: refreshedData, error: refreshError } = await supabase
        .from('login')
        .select('*');

      if (refreshError) {
        throw refreshError;
      }

      setLoginData(refreshedData);
    } catch (error) {
      console.error('Error editing account:', error.message);
      setErrorMessage(error.message);
    }
  };

  return (
    <div className="register-container">
      <h2>User Management</h2>
      {owner && ( //  owner  true 
        <table className="login-table">
          <thead>
            <tr>
              <th>Username</th>
              <th>Email</th>
              <th>Scope</th>
              <th>Edit</th>
              <th>Delete</th>
            </tr>
          </thead>
          <tbody>
            {loginData.map((login, index) => (
              <tr key={index}>
                <td>{login.name}</td>
                <td>{login.email}</td>
                <td>{login.scope}</td>
                <td>
                  <button className="edit-button" onClick={() => {
                    setEditingUser(login.name);
                    setEditName(login.name);
                    setEditEmail(login.email);
                    setEditPermission(login.scope);
                    setShowEditPopup(true);
                  }}>Edit</button>
                </td>
                <td>
                  <button className="delete-button" onClick={() => {
                    setDeletingUser(login.name);
                    setShowConfirmationPopup(true);
                  }}>Delete</button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
      {owner && ( //  owner  true 
        <button className="register-button" onClick={() => setShowRegisterPopup(true)}>Register</button>
      )}
      {showRegisterPopup && (
        <div className="register-popup">
          <div className="register-form">
            <span className="close-button" onClick={() => setShowRegisterPopup(false)}>X</span>
            <h2>Register</h2>
            <form onSubmit={handleRegister}>
              <label htmlFor="username">Username:</label>
              <input
                type="text"
                id="username"
                value={name}
                onChange={(e) => setUsername(e.target.value)}
              />
              <label htmlFor="password">Password:</label>
              <input
                type="password"
                id="password"
                value={pin}
                onChange={(e) => setPassword(e.target.value)}
              />
              <label htmlFor="email">Email:</label>
              <input
                type="email"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
              {errorMessage && <div className="error-message">{errorMessage}</div>}
              <button type="submit">Register</button>
            </form>
          </div>
        </div>
      )}
      {showEditPopup && (
        <div className="register-popup">
          <div className="register-form">
            <span className="close-button" onClick={() => setShowEditPopup(false)}>X</span>
            <h2>Edit</h2>
            <form onSubmit={handleEdit}>
              <label htmlFor="edit-username">Username:</label>
              <input
                type="text"
                id="edit-username"
                value={editName}
                onChange={(e) => setEditName(e.target.value)}
              />
              <label htmlFor="edit-email">Email:</label>
              <input
                type="email"
                id="edit-email"
                value={editEmail}
                onChange={(e) => setEditEmail(e.target.value)}
              />
              {errorMessage && <div className="error-message">{errorMessage}</div>}
              <div className="button-group">
                <button type="submit">Save</button>
                <button onClick={() => setShowEditPopup(false)}>Cancel</button>
              </div>
            </form>
          </div>
        </div>
      )}
      {showConfirmationPopup && (
        <div className="confirmation-popup">
          <div className="confirmation-content">
            <h2>Confirm Deletion</h2>
            <p>Are you sure you want to delete the user "{deletingUser}"?</p>
            <div className="button-group">
              <button className="confirm-button" onClick={handleDelete}>Confirm</button>
              <button className="cancel-button" onClick={() => setShowConfirmationPopup(false)}>Cancel</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default Register;